import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";

import { SpecialtiesService } from './specialties.service';

import { SpecialtiesComponent } from './specialties/specialties.component';

import { UsersWidgetModule } from '../+users/_widgets/users.widget';

const SPECIALTIES_ROUTES: Routes = [
  { path: '', component: SpecialtiesComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(SPECIALTIES_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    UsersWidgetModule
  ],
  declarations: [SpecialtiesComponent],
  providers: [SpecialtiesService],
  exports: [SpecialtiesComponent]
})
export class SpecialtiesModule { }



