import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

import { ToolbarService } from '../../shared/toolbar.service';
import { SpecialtiesService } from '../specialties.service';

import { PaginationService } from '../../shared/pagination.service';
import { ToastService } from '../../shared/toast.service';
import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-specialties',
  templateUrl: './specialties.component.html',
  styleUrls: ['./specialties.component.css']
})
export class SpecialtiesComponent implements OnInit {
  public specialties: any[];
  public currentItem: any;
  public modalDeleteActions = new EventEmitter<string|MaterializeAction>();
  public modalDetailActions = new EventEmitter<string|MaterializeAction>();
  public modalAddActions = new EventEmitter<string|MaterializeAction>();

  constructor(
    private _specialtiesService: SpecialtiesService,
    private _toastService: ToastService,
    private _toolbar: ToolbarService,
    private _pager: PaginationService,
    private _router: Router
  ) { }

  ngOnInit() {
    let self = this;
    this._pager.clear();

    this._toolbar.change({
      title: "Список специальностей",
      name: "specialties",
      actions: {
        add: () => this.openModal({name: '', active: true}, 'add')
      },
      actionsPosition: {
        extend: ['add']
      }
    });
     
    this._specialtiesService.get()
      .subscribe((response) => {
        this.specialties = response;
      });
  }

  openModal(item, modalName?) {
    this.currentItem = item;
    modalName = modalName ? modalName : 'delete';
    
    switch(modalName) {
      case 'delete':
        this.modalDeleteActions.emit({action:"modal",params:['open']});
        break;
      case 'detail':
        this.modalDetailActions.emit({action:"modal",params:['open']});
        break;
      case 'add':
        this.modalAddActions.emit({action:"modal",params:['open']});
        break;
    }
    
  }

  closeModal(modalName?) {
    modalName = modalName ? modalName : 'delete';
    switch(modalName) {
      case 'delete':
        this.modalDeleteActions.emit({action:"modal",params:['close']});
        break;
      case 'detail':
        this.modalDetailActions.emit({action:"modal",params:['close']});
        break;
      case 'add':
        this.modalAddActions.emit({action:"modal",params:['close']});
        break;
    }
  }

  deleteItem(item) {
    let self = this;

    this._specialtiesService.remove(item._id)
      .subscribe((data: any) => {
        if(data.status == 'ok') {
          this.specialties.find((service, index) => {
            if(item._id == service._id) {
              self.currentItem = null;
              self.specialties.splice(index, 1);
              return true;
            }
          });
          this._toastService.toast("Специальность удалена");
        }
      });
  }

  updateItem(item) {
    let self = this;

    this._specialtiesService.update(item)
      .subscribe((data: any) => {
          this._toastService.toast("Специальность обновлена");
      });
  }

  addItem(item) {
    let self = this;

    this._specialtiesService.add(item)
      .subscribe((data: any) => {
          this._toastService.toast("Специальность добавлена");
          this.specialties.unshift(data);
      });
  }

}



